<template>
  <div id="app" :style="'transition: all 0.6s;top: ' + (-cnt * height) + 'px'">
    <Navigator @gotarget="getCnt"></Navigator>
    <section class="slider"><Banner></Banner></section>
    <section class="slider" id="city">
      <Title title="" :url="require('./assets/city_traffic.png')" v-if="showTitle"></Title>
      <Item direction="left" :url="require('./assets/city.jpg')">
        <h3>DCC EYE </h3>
        <p></p>
      </Item>
    </section>
    <section class="slider" id="drive">
       <Title title="" :url="require('./assets/drive_assistance.png')" v-show="showTitle"></Title>
      <Item direction="right" :url="require('./assets/drive.jpg')">
        <h3>ADAS</h3>
        <p>ADAS SDKADAS</p>
      </Item>
    </section>
    <section class="slider" id="face">
      <Title title="" :url="require('./assets/micro_expression.png')" v-show="showTitle"></Title>
      <Item direction="left" :url="require('./assets/face.jpg')">
        <h3>FACE CODE </h3>
        <p></p>
      </Item>
    </section>
    <section class="slider" id="service">
       <Title title="" :url="require('./assets/car_service.png')" v-show="showTitle"></Title>
      <Item direction="right" :url="require('./assets/map.jpg')">
        <h3></h3>
        <p></p>
      </Item>
    </section>
    <section class="slider" id="aboutUs">
      <Foot></Foot>
    </section>
  </div>
</template>

<script>
import Navigator from './components/Navigator'
import Banner from './components/Banner'
import Item from './components/Item'
import Title from './components/Title'
import Foot from './components/Foot'

export default {
  name: 'app',
  components: {
    Navigator, Banner, Item, Title, Foot
  },
  data () {
    return {
      showTitle: true,
      height: 0,
      width: 0,
      cnt: 0
    }
  },
  methods: {
    getCnt (n) {
      this.cnt = n
    }
  },
  mounted () {
    const app = document.querySelector('#app')
    const sections = document.querySelectorAll('.slider')
    this.width = window.innerWidth
    this.height = window.innerHeight
    if (window.innerWidth > 800) {
      Array.prototype.forEach.call(sections, (section) => {
        section.style.height = window.innerHeight + 'px'
      })
    } else if (this.width < 800) {
      this.showTitle = false
    }
    window.addEventListener('resize', () => {
      [].forEach.call(sections, (section) => {
        section.style.height = this.height + 'px'
      })
      this.width = window.innerWidth
      if (this.width < 800) {
        this.showTitle = false
      } else if (this.width > 800) {
        this.showTitle = true
      }
      if (this.height !== window.innerHeight) {
        app.style.top = this.cnt * this.height + 'px'
      }
      this.height = window.innerHeight
    })
    let flag = true
    window.addEventListener('DOMMouseScroll', mouseWheel.bind(this))
    window.addEventListener('mousewheel', mouseWheel.bind(this))
    function mouseWheel (e) {
      let delta = e.wheelDelta || -e.detail
      if (flag) {
        flag = false
        setTimeout(() => {
          flag = true
        }, 620)
        if (delta > 0 && this.cnt > 0) {
          this.cnt--
        } else if (delta < 0 && this.cnt < 5) {
          this.cnt++
        }
      }
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
}
body {
  margin: 0;
  font-family: "Helvetica", "Arial", sans-serif;
  height: 100%;
  overflow: hidden;
}
html {
  height: 100%;
}
.slider{
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
@media screen and (max-width: 800px) {
  #app{
    position: static;
  }
  body {
    margin: 0;
    font-family: "Helvetica", "Arial", sans-serif;
    height: 100%;
    overflow: scroll;
  }
}
</style>



